export default {
	name:"File",
	data(){
		return{
			subOff:true,
			fileArr:[{},{}]
		}
	},
	methods:{
		btn(){
			
		},
		photo(index,ev){
		    this.fileArr[index]=ev.target.files[0];
		   var newArr= this.fileArr[index].type.split('/');
		   if(newArr[0]!="image"){
		   	  alert("请上传图片")
		   }else{
		   	   this.subOff=false
		   }
		   
		},
		video(index,ev){
		    this.fileArr[index]=ev.target.files[0];
		    var newArr= this.fileArr[index].type.split('/');
		    console.log(newArr)
		   if(newArr[0]!="video"){
		   	  alert("请上传视频")
		   }else{
		   	 this.subOff=false
		   }
		}
	}
}



