	import Welcome from "@/components/Welcome"
export default {
  name: 'App',
  components:{
  	Welcome
  },
  data(){
  	return{
  		  	welcome:true
  	}
  },
    created(){
    	if(this.$route.path=="/"){
    		 setTimeout(()=>{
		  			this.$router.push('/index');
		  			this.welcome=false
		  	},8000)
    	}else{
    				this.welcome=false
    	}
 	
  },
  watch:{
     $route:{
     		handler(){
  		     	if(this.$route.path=="/"){
		    		 setTimeout(()=>{
				  			this.$router.push('/index');
				  			this.welcome=false
				  	},5000)
		    	}else{
		    				this.welcome=false
    				}
  			}
     }
  }
}



