import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome'
import Index from '@/components/index'
import File from '@/components/file'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
      {
      path: '/index',
      name: 'Index',
      component: Index
    },
      {
      path: '/file',
      name: 'File',
      component: File
    }
  ]
})



