
import Swiper from 'vux/src/components/swiper/swiper.vue'

import img_1 from "@/assets/image/1.jpg"
import img_2 from "@/assets/image/2.jpg"
import img_3 from "@/assets/image/3.jpg"
import img_4 from "@/assets/image/4.jpg"
import img_5 from "@/assets/image/5.jpg"
export default{
components:{
Swiper
},
data(){
return {
subOff:true,
list:[
{
url: 'javascript:',
img: img_1,
title: '九寨沟景区'
},
{
url: 'javascript:',
img: img_2,
title: '九寨沟景区'
},{
url: 'javascript:',
img: img_3,
title: '九寨沟景区'
},{
url: 'javascript:',
img: img_4,
title: '九寨沟景区'
},
{
url: 'javascript:',
img: img_5,
title: '九寨沟景区'
}
],
arrTag:["服务好","态度棒","千篇一律","看人海","前篇一律","服务好","态度棒","千篇一","前篇一律","看人海",],
scoreArr:[0,0,0],
subDes:'请给景区评分',
subdesOff:false,
tagdec:[],
bjShow:false
}
},
methods:{
checked(index,ev,rows){

var aChild=ev.target.parentNode.children;
for(var i=0;i<aChild.length;i++){
if(i<=index){
aChild[i].style.backgroundPosition="0,0"
}else{
aChild[i].style.backgroundPosition="-38px,0"
}
}
this.scoreArr[rows]=index+1;
console.log(this.scoreArr)
this.subOff=false
},
tagCheck(index,ev,item){
var classIndex=ev.target.className.indexOf("tag-active")
var classArr=ev.target.className.split(' ');
if(classIndex!=-1){
classArr.pop("tag-active")
ev.target.className=classArr.join('')
this.tagdec.splice(this.tagdec.indexOf(item),1)
}else{
classArr.push("tag-active");
ev.target.className=classArr.join(' ')
this.tagdec.push(item)
}
if(classArr.indexOf('tag-active')!=-1){
this.subOff=false
}else{
this.subOff=true
}

},
btn(){
let check = this.scoreArr.some(item=>{
return item==0
})
if(check){
this.subdesOff=true
setTimeout(()=>{
this.subdesOff=false
},2000)
}else{
this.subdesOff=false

if(!this.tagdec.length){
this.subDes='请给景区添加标签'
this.subdesOff=true
setTimeout(()=>{
this.subdesOff=false
},2000)
}else{
this.bjShow=true
setTimeout(()=>{
this.$router.push('/file')
},3000)

}

}
}

}
}



